import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useParams } from 'react-router-dom'; // Import useParams from react-router-dom
import './portfolio.css';
import CallbackPopup from "../../mainUtils/CallbackPopup";

export default function Portfolio() {
  const { category } = useParams(); // Get the category from the URL parameter
  const [imageLinks, setImageLinks] = useState([]);

  useEffect(() => {
    fetchImageLinks();
  }, [category]); // Fetch image links whenever the category changes

  const fetchImageLinks = async () => {
    try {
      const response = await axios.get(`https://backend-wine-six.vercel.app/portfolio/${category}/images`);
      console.log('Image Links:', response.data); // Log the image links data to the console
      const imageLinksArray = response.data.map((imageData) => imageData.link);
      setImageLinks(imageLinksArray);
    } catch (error) {
      console.error('Error fetching image links:', error);
    }
  };

  return (
    <div>
      <h2 className='text-center text-capitalize mt-4'>{category} Portfolio</h2>
      <div className="newgallery">
        {imageLinks.map((link, index) => (
          <img key={index} src={link} alt={`Image ${index + 1}`} />
        ))}
      </div>

      <CallbackPopup popup={true} callBackForm={true} />
    </div>
  );
}
